import './ScoreContNoteTab.css';

// import TabbedPane from './common/TabbedPane.jsx';
import { DetailChord, DetailSection, EditMode, FloatWndState, Store, TypeNoteElement } from 'contents/redux/store';
import { GlobalContext } from 'contents/MainFrame';
import React, { useContext, KeyboardEvent, useEffect, useState, useRef } from 'react';

import ScoreElement, { ProgressInfo } from './element/ScoreElement';
import { ActionType } from 'contents/redux/reducer';
import OutlineSection from './OutlineSection';
import { KeyboardShortCutAction } from '../../../../operation/InputNoteTab';
import NoteContextMenu from './floatwnd/context/NoteContextMenu';
import ChordSelector from './floatwnd/chord/ChordSelector';
import CurrentElementInfo from './sideDetail/CurrentElementInfo';
import BackingEditor from './floatwnd/backing/BackingEditor';
import styled from 'styled-components';

const ScoreContNoteTab = () => {
    const { state, dispatch } = useContext(GlobalContext);

    // /**  */
    // const [elementList, setElementList] = useState([] as JSX.Element[]);
    // /**  */
    // const [sectionList, setSectionList] = useState([] as JSX.Element[]);

    // useEffect(() => {
    // alert(state.noteData.dataList.length);
    const dataList = state.noteData.dataList;
    const editMode = state.noteState.editMode;
    // const isNormalMode = editMode == EditMode.NORMAL;
    let index = 0;
    const elementList: JSX.Element[] = [];
    const sectionList: JSX.Element[] = [];

    let no = 1;
    let totalBeat = 0;
    let totalMsec = 0;
    let lastBar = -1;
    dataList.forEach((data, i) => {
        const bar = Math.floor(totalBeat / 4) + 1;
        const progress = {} as ProgressInfo;
        progress.no = no;
        progress.barCount = lastBar != bar ? bar : -1;
        progress.remaindBeat = totalBeat % 4 > 0 ? totalBeat % 4 : -1;
        progress.headMSec = totalMsec;
        elementList.push(
            <ScoreElement
                key={i}
                index={i}
                noteElement={data}
                progress={progress}
            />
        );
        if (data.type == TypeNoteElement.Chord) {
            lastBar = bar;
            const detail = data.detail as DetailChord;

            const tempo = 80;
            totalBeat += detail.sustain;
            const sectionMSec = 60000 / tempo * 4;
            totalMsec += sectionMSec / 4 * detail.sustain;
            no++;
        }

        if (data.type == TypeNoteElement.Section) {
            sectionList.push(
                <OutlineSection key={i} index={index} label={(data.detail as DetailSection).label} />
            );
            index++;
        }
    });
    //     setElementList(elementList);
    //     setSectionList(sectionList);
    // }, [state.noteData.dataList]);

    const isModalBlind = !(state.noteState.floatwndState == FloatWndState.NONE || state.noteState.floatwndState == FloatWndState.CONTEXT_MENU);
    return (
        <div id="scorecont-notetab-root">
            <div className="left-cont">
                <div id="score-note">
                    <div
                        id="operation-header"
                        className={editMode == EditMode.BACKING ? 'mode-selector' : ''}
                    >
                        <button className="disable"><span>BACK</span></button>
                        <button className="disable"><span>SAVE</span></button>
                        <button className="disable"><span>RELOAD</span></button>
                        <button className="disable"><span>OPTION</span></button>
                        <EditModeBorder />
                    </div>
                    <div
                        id="section-list"
                        className={editMode == EditMode.BACKING ? 'mode-selector' : ''}
                    >
                        {sectionList}
                    </div>
                    <div
                        id="score-list-root"
                        className={editMode == EditMode.BACKING ? 'mode-selector' : ''}
                    >
                        {elementList}
                    </div>
                    {/*  */}
                    <div id="modal-blind"
                        className={isModalBlind ? 'active' : ''}
                        onClick={() => {
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                        }}
                    />
                    {/*  */}
                    <NoteContextMenu />
                    <ChordSelector />
                    <BackingEditor />
                </div>
                <div id="score-footer">
                </div>
            </div>
            <div className="right-cont">
                <CurrentElementInfo />
                {/* <div id="sidebar-param-selector" className="hidden" tabIndex="999">
                </div> */}
            </div>
        </div>
    );
}

const EditModeBorder = () => {
    const { state, dispatch } = useContext(GlobalContext);

    const editMode = state.noteState.editMode;
    // const isNormal = editMode == EditMode.NORMAL;
    // const setEditMode = (editMode: EditMode) => {
    //     dispatch(ActionType.SET_NOTE_STATE, { noteState: { ...state.noteState, editMode } });
    // };
    return (
        <div className="mode">
            <div className="label"><span>MODE:</span></div>
            <div className="border">
                <button
                    className={editMode == EditMode.NORMAL ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.NORMAL)}
                >
                    <span>NORMAL</span>
                </button>
                <button
                    className={editMode == EditMode.BACKING ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.BACKING)}
                >
                    <span>CLONE</span>
                </button>
                <button
                    className={editMode == EditMode.PREVIEW ? 'active' : ''}
                // onClick={() => setEditMode(EditMode.BACKING)}
                >
                    <span>PREVIEW</span>
                </button>
            </div>
        </div>
    );
}

export default ScoreContNoteTab;


const Title = styled.div({
    display: 'inline-block',
    margin: '2px 15px 0 15px',
    color: 'rgba(255, 0, 0, 0.5)',
    fontSize: '12px',
    fontWeight: 600,
});
